import React, {FC} from 'react';
import {
  Cell,
  Grid,
  Offset,
  Row,
  Section,
  Spacer,
  Accordion,
} from '@excelwithbusiness/webmobile-sc-components';
// @ts-ignore-next-line
import {CmsCallToActionPanel} from "./CallToActionPanel";
import {PlaylistSingleSection} from "./PlaylistsSection";
import {isArray, length} from '@/utils/FP/FP';

export const Content = ({slices}) => {
    if (!isArray(slices) || !length(slices, 0)) {
        return null;
    }

    return (
        <Section>
            {slices?.map((slice: any, index: number) => {
                const keyProp = `${slice?.type}_${index}`;
                switch (slice?.type) {
                    case 'CallToAction':
                      console.log('matched CallToAction')
                    return <CmsCallToActionPanel content={slice.pageComponents} />
                  case 'PlaylistSection':
                      console.log('matched PlaylistSection')
                    return <PlaylistSingleSection key={keyProp} playlistName={slice.name} title={slice.pageComponents.sectionName} playlistId={slice.id} toolTip="Not found where this us yet!!" desktop={slice.size} staticAssets={[...slice.pageComponents.staticPlaylists[0].staticAssetTiles]} />
                    default:
                        return null;
                }
            })}
        </Section>
    )
};
